import React from 'react'
import unslug from 'unslug';
import styled from 'styled-components'

import { Link } from 'react-router-dom';

const StyledMenuLink = styled(Link)`
    margin-bottom: 10px;
    padding: 10px;
    transition: border 0.5s;
    z-index: 2;
    color: ${ props => props.active ? '#282c34' : '#fff' };
    text-decoration: none;
`

class NewLink extends Link {
    onMouseOver = () => {
        const { label, onMouseOver } = this.props;
        onMouseOver(label);
    }
    
    render() {
        const {
            onMouseOver,
            props: {
                label,
                activeLink
            },
        } = this;

        const isActive = label == activeLink ? true : false;
        
        return (
            <StyledMenuLink
                onMouseOver={onMouseOver}
                id={label}
                to={label}
                active={isActive}
            >
                {unslug(label)}
            </StyledMenuLink>

        )
    }
}

export default NewLink