import React, { Component } from 'react';
import styled from 'styled-components'

import Link from './Link';

const Border = styled.div`
    position: absolute;
    width: 0px;
    height: 0px;
    transition: width 0.2s, top 0.2s cubic-bezier(0,.99,.47,.9);
    background-color: mediumspringgreen;
`

class NewMenu extends Component {
    constructor(props){
        super(props);
        this.state = {
            activeLink: ''
        }
    }

    componentDidUpdate = () => {
        const {
            handleBorderReposition,
            state: {
                activeLink,
            }
        } = this;

        const activeComponent = document.querySelector(`#${activeLink}`);
        handleBorderReposition(activeComponent);
    }

    handleBorderReposition = border => {
        const rect = border.getBoundingClientRect();
        this.repositionBorder(rect)
    }

    repositionBorder = ({top, height, width}) => {
        const border = document.querySelector('#border');
        border.style.top = `${top - 2}px`;
        border.style.height = `${height}px`;
        border.style.width = `${width}px`;
    }

    onMouseOverLinkItem = link => {
        this.setState({
            activeLink: link
        })
    }

    render() {
        const {
            onMouseOverLinkItem,
            props: {
                children,
            },
            state: {
                activeLink,
            }
        } = this;

        return (
            <>
            {
                React.Children.map(this.props.children, child=>{
                    const { label } = child.props;

                    return (
                        <Link 
                            key={label}
                            label={label}
                            onMouseOver={onMouseOverLinkItem}
                            activeLink={activeLink}
                        />
                    )
                })
            }
            <Border id="border"/>
            </>
        )
    }
}

export default NewMenu