import React from 'react';
import { Home } from './views/Home';
import { StoryMode } from './views/StoryMode';
import { ChapterSelect } from './views/ChapterSelect';
import { Credits } from './views/Credits';
import { NoMatch } from './views/NoMatch';
import { NavBar } from './components/NavBar';
import { Route, Switch, Redirect } from 'react-router-dom';
import { TransitionGroup, CSSTransition } from "react-transition-group";
import styled from 'styled-components'

const Body = styled.div`
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
`

export const Routes = () => {
    return (
        <div>
            <NavBar />
            <Body>
                <Switch>
                    <Route exact path="/" component={Home}></Route>
                    <Route exact path="/story-mode" component={StoryMode}></Route>
                    <Route exact path="/chapter-select" component={ChapterSelect}></Route>
                    <Route exact path="/credits" component={Credits}></Route>
                    <Route component={NoMatch}></Route>
                </Switch>
            </Body>
        </div>
    )
}